import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { loginUserThunk, logoutThunk } from "./authThunks";
import { jwtDecode } from "jwt-decode";
import { fetchCartThunk } from "../cart/cartThunk";

// Interface ƒë·ªãnh nghƒ©a user l·∫•y t·ª´ payload token
import { User } from "@/types/userType";

//  Interface ƒë·ªãnh nghƒ©a c·∫•u tr√∫c state
interface AuthState {
  token: string | null;
  user: User | null;
  loading: boolean;
  error: string | null;
}

//  State kh·ªüi t·∫°o
const initialState: AuthState = {
  token: null,
  user: null,
  loading: false,
  error: null,
};

//  T·∫°o Slice
const authSlice = createSlice({
  name: "auth",
  initialState,
  reducers: {
    clearError: (state) => {
      state.error = null;
    },
    // D√πng khi reload trang (ReduxProvider kh√¥i ph·ª•c)
    setCredentials: (
      state,
      action: PayloadAction<{ token: string; user: User }>
    ) => {
      state.token = action.payload.token;
      state.user = action.payload.user;
    },
  },
  extraReducers: (builder) => {
    //  LOGIN
    builder
      .addCase(loginUserThunk.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(loginUserThunk.fulfilled, (state, action, reducerApi) => {
        const { dispatch } = reducerApi;

        state.loading = false;
        state.token = action.payload;

        try {
          const decodedUser = jwtDecode<User>(action.payload);
          state.user = decodedUser;

          // üü¢ G·ªçi API l·∫•y gi·ªè h√†ng
          dispatch(
            fetchCartThunk({ userId: decodedUser.id, token: action.payload })
          );

          localStorage.setItem("token", action.payload);
          localStorage.setItem("user", JSON.stringify(decodedUser));
        } catch {
          console.error("Token kh√¥ng h·ª£p l·ªá");
        }
      })

      .addCase(loginUserThunk.rejected, (state, action) => {
        state.loading = false;
        state.error = action.error.message || "ƒêƒÉng nh·∫≠p th·∫•t b·∫°i";
      });

    //  LOGOUT
    builder.addCase(logoutThunk.fulfilled, (state) => {
      state.token = null;
      state.user = null;
      state.error = null;

      localStorage.removeItem("token");
      localStorage.removeItem("user");
    });
  },
});

export const { clearError, setCredentials } = authSlice.actions;
export default authSlice.reducer;
